import neo4j, { Driver, Session } from 'neo4j-driver';

export interface Neo4jConfig {
  uri: string;
  username: string;
  password: string;
  database?: string;
}

export class Neo4jClient {
  private driver: Driver;

  constructor(private config: Neo4jConfig) {
    this.driver = neo4j.driver(
      config.uri,
      neo4j.auth.basic(config.username, config.password),
      {
        maxConnectionLifetime: 3 * 60 * 60 * 1000, // 3 hours
        maxConnectionPoolSize: 50,
        connectionAcquisitionTimeout: 2 * 60 * 1000, // 120 seconds
      }
    );
  }

  async verifyConnectivity(): Promise<void> {
    const session = this.driver.session();
    try {
      await session.run('RETURN 1');
      // Connection verified successfully
    } finally {
      await session.close();
    }
  }

  getSession(database?: string): Session {
    return this.driver.session({
      database: database || this.config.database || 'neo4j',
    });
  }

  async executeQuery<T = unknown>(
    query: string,
    parameters: Record<string, unknown> = {},
    database?: string
  ): Promise<T[]> {
    const session = this.getSession(database);
    try {
      const result = await session.run(query, parameters);
      return result.records.map((record) => record.toObject() as T);
    } finally {
      await session.close();
    }
  }

  async executeWrite<T = unknown>(
    query: string,
    parameters: Record<string, unknown> = {},
    database?: string
  ): Promise<T[]> {
    const session = this.getSession(database);
    try {
      const result = await session.writeTransaction(async (tx) =>
        tx.run(query, parameters)
      );
      return result.records.map((record) => record.toObject() as T);
    } finally {
      await session.close();
    }
  }

  async close(): Promise<void> {
    await this.driver.close();
  }
}

export async function createNeo4jClient(): Promise<Neo4jClient> {
  const uri = process.env.NEO4J_URI || '';
  const username = process.env.NEO4J_USERNAME || 'neo4j';
  const password = process.env.NEO4J_PASSWORD || '';

  if (!uri || !password) {
    throw new Error('Neo4j connection details not configured');
  }

  const client = new Neo4jClient({
    uri,
    username,
    password,
    database: process.env.NEO4J_DATABASE,
  });

  await client.verifyConnectivity();
  return client;
}