/**
 * Multi-modal LLM Service with Gemini 2.5 Pro and Grok-4 support
 * Supports text and image inputs for both providers
 */

import { VertexAI } from '@google-cloud/vertexai';

export type LLMProvider = 'gemini' | 'grok';

// Gemini content part types
interface GeminiTextPart {
  text: string;
}

interface GeminiInlineDataPart {
  inlineData: {
    mimeType: string;
    data: string;
  };
}

type GeminiPart = GeminiTextPart | GeminiInlineDataPart;

// OpenAI/Grok content part types
interface OpenAITextPart {
  type: 'text';
  text: string;
}

interface OpenAIImagePart {
  type: 'image_url';
  image_url: {
    url: string;
    detail?: 'low' | 'high' | 'auto';
  };
}

type OpenAIPart = OpenAITextPart | OpenAIImagePart;

export interface LLMMessage {
  role: 'system' | 'user' | 'assistant';
  content: string | MultiModalContent[];
}

export interface MultiModalContent {
  type: 'text' | 'image';
  text?: string;
  imageUrl?: string;
  mimeType?: string;
}

export interface LLMResponse {
  content: string;
  usage?: {
    promptTokens: number;
    completionTokens: number;
    totalTokens: number;
  };
  provider: LLMProvider;
  model: string;
  finishReason?: string;
}

export interface LLMConfig {
  provider: LLMProvider;
  model?: string;
  temperature?: number;
  maxTokens?: number;
  topP?: number;
  stream?: boolean;
}

export class LLMService {
  private geminiClient: VertexAI | null = null;
  private grokApiKey: string | null = null;

  constructor() {
    // Initialize Gemini client
    const geminiApiKey = process.env.GEMINI_API_KEY;
    if (geminiApiKey) {
      this.geminiClient = new VertexAI({
        project: process.env.MARIA_PROJECT_ID || 'maria-code',
        location: 'us-central1'
      });
    }

    // Store Grok API key
    this.grokApiKey = process.env.GROK_API_KEY || null;
  }

  /**
   * Generate completion using specified LLM provider
   */
  async generateCompletion(
    messages: LLMMessage[],
    config: LLMConfig = { provider: 'gemini' }
  ): Promise<LLMResponse> {
    const { provider, temperature = 0.7, maxTokens = 2048, stream = false } = config;

    switch (provider) {
      case 'gemini':
        return this.generateGeminiCompletion(messages, {
          model: config.model || 'gemini-2.5-pro-preview-06-05',
          temperature,
          maxTokens,
          stream
        });
      
      case 'grok':
        return this.generateGrokCompletion(messages, {
          model: config.model || 'grok-4-latest',
          temperature,
          maxTokens,
          stream
        });
      
      default:
        throw new Error(`Unsupported LLM provider: ${provider}`);
    }
  }

  /**
   * Generate completion using Gemini 2.5 Pro with multimodal support
   */
  private async generateGeminiCompletion(
    messages: LLMMessage[],
    config: { model: string; temperature: number; maxTokens: number; stream: boolean }
  ): Promise<LLMResponse> {
    if (!this.geminiClient) {
      throw new Error('Gemini client not initialized. Check GEMINI_API_KEY environment variable.');
    }

    const { model, temperature } = config;

    // Convert messages to Gemini format
    const contents = messages
      .filter(msg => msg.role !== 'system') // Gemini handles system messages differently
      .map(msg => ({
        role: msg.role === 'assistant' ? 'model' : 'user',
        parts: this.convertContentToParts(msg.content)
      }));

    // Handle system message as part of the first user message
    const systemMessage = messages.find(msg => msg.role === 'system');
    if (systemMessage && contents.length > 0 && contents[0]) {
      const systemParts = this.convertContentToParts(systemMessage.content);
      if (contents[0].role === 'user' && contents[0].parts) {
        contents[0].parts = [...systemParts, ...contents[0].parts];
      }
    }

    const generationConfig = {
      temperature,
      maxOutputTokens: config.maxTokens,
      responseMimeType: 'text/plain',
    };

    try {
      if (config.stream) {
        const genModel = this.geminiClient.getGenerativeModel({ model });
        const result = await genModel.generateContentStream({
          contents,
          generationConfig
        });

        let fullContent = '';
        for await (const chunk of result.stream) {
          const chunkText = chunk.candidates?.[0]?.content?.parts?.[0]?.text;
          if (chunkText) {
            fullContent += chunkText;
          }
        }

        return {
          content: fullContent,
          provider: 'gemini',
          model,
          finishReason: 'stop'
        };
      } else {
        const genModel = this.geminiClient.getGenerativeModel({ model });
        const result = await genModel.generateContent({
          contents,
          generationConfig
        });

        const response = await result.response;
        const content = response.candidates?.[0]?.content?.parts?.[0]?.text || '';
        
        return {
          content,
          provider: 'gemini',
          model,
          usage: {
            promptTokens: 0, // Gemini doesn't provide token counts in this format
            completionTokens: 0,
            totalTokens: 0
          },
          finishReason: 'stop'
        };
      }
    } catch (error) {
      // eslint-disable-next-line no-console
      console.error('Gemini API error:', error);
      throw new Error(`Gemini API error: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  /**
   * Generate completion using Grok-4 with multimodal support
   */
  private async generateGrokCompletion(
    messages: LLMMessage[],
    config: { model: string; temperature: number; maxTokens: number; stream: boolean }
  ): Promise<LLMResponse> {
    if (!this.grokApiKey) {
      throw new Error('Grok API key not found. Check GROK_API_KEY environment variable.');
    }

    const { model, temperature, maxTokens, stream } = config;

    // Convert messages to OpenAI format (Grok uses OpenAI-compatible API)
    const formattedMessages = messages.map(msg => ({
      role: msg.role,
      content: this.convertContentToOpenAIFormat(msg.content)
    }));

    const requestBody = {
      model,
      messages: formattedMessages,
      temperature,
      max_tokens: maxTokens,
      stream,
      top_p: 1.0,
    };

    try {
      const response = await fetch('https://api.x.ai/v1/chat/completions', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.grokApiKey}`,
        },
        body: JSON.stringify(requestBody),
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`Grok API error (${response.status}): ${errorText}`);
      }

      if (stream) {
        // Handle streaming response
        const reader = response.body?.getReader();
        if (!reader) {
          throw new Error('Failed to get response reader');
        }

        let fullContent = '';
        const decoder = new TextDecoder();

        while (true) {
          const { done, value } = await reader.read();
          if (done) break;

          const chunk = decoder.decode(value);
          const lines = chunk.split('\n').filter(line => line.trim() !== '');

          for (const line of lines) {
            if (line.startsWith('data: ')) {
              const data = line.slice(6);
              if (data === '[DONE]') continue;

              try {
                const parsed = JSON.parse(data);
                const content = parsed.choices?.[0]?.delta?.content;
                if (content) {
                  fullContent += content;
                }
              } catch (e) {
                // eslint-disable-next-line no-console
                console.warn('Failed to parse streaming chunk:', e);
              }
            }
          }
        }

        return {
          content: fullContent,
          provider: 'grok',
          model,
          finishReason: 'stop'
        };
      } else {
        const data = await response.json();
        const choice = data.choices?.[0];
        
        if (!choice) {
          throw new Error('No choices returned from Grok API');
        }

        return {
          content: choice.message?.content || '',
          provider: 'grok',
          model,
          usage: data.usage ? {
            promptTokens: data.usage.prompt_tokens,
            completionTokens: data.usage.completion_tokens,
            totalTokens: data.usage.total_tokens
          } : undefined,
          finishReason: choice.finish_reason
        };
      }
    } catch (error) {
      // eslint-disable-next-line no-console
      console.error('Grok API error:', error);
      throw new Error(`Grok API error: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  /**
   * Convert content to Gemini parts format
   */
  private convertContentToParts(content: string | MultiModalContent[]): GeminiPart[] {
    if (typeof content === 'string') {
      return [{ text: content }];
    }

    return content.map(item => {
      switch (item.type) {
        case 'text':
          return { text: item.text || '' };
        case 'image':
          if (item.imageUrl) {
            // For Gemini, we need to handle image data differently
            // This is a simplified implementation - in production, you'd want to properly handle base64 data
            return {
              inlineData: {
                mimeType: item.mimeType || 'image/jpeg',
                data: item.imageUrl.includes('base64,') 
                  ? item.imageUrl.split('base64,')[1] || ''
                  : item.imageUrl
              }
            };
          }
          return { text: '[Image]' };
        default:
          return { text: '[Unsupported content type]' };
      }
    });
  }

  /**
   * Convert content to OpenAI format for Grok
   */
  private convertContentToOpenAIFormat(content: string | MultiModalContent[]): string | OpenAIPart[] {
    if (typeof content === 'string') {
      return content;
    }

    // For multimodal content, use OpenAI's format
    return content.map(item => {
      switch (item.type) {
        case 'text':
          return {
            type: 'text',
            text: item.text || ''
          };
        case 'image':
          return {
            type: 'image_url',
            image_url: {
              url: item.imageUrl || '',
              detail: 'high'
            }
          };
        default:
          return {
            type: 'text',
            text: '[Unsupported content type]'
          };
      }
    });
  }

  /**
   * Check if a provider is available (has required API keys)
   */
  isProviderAvailable(provider: LLMProvider): boolean {
    switch (provider) {
      case 'gemini':
        return !!this.geminiClient;
      case 'grok':
        return !!this.grokApiKey;
      default:
        return false;
    }
  }

  /**
   * Get available providers
   */
  getAvailableProviders(): LLMProvider[] {
    const providers: LLMProvider[] = [];
    
    if (this.isProviderAvailable('gemini')) {
      providers.push('gemini');
    }
    
    if (this.isProviderAvailable('grok')) {
      providers.push('grok');
    }
    
    return providers;
  }

  /**
   * Get supported models for a provider
   */
  getSupportedModels(provider: LLMProvider): string[] {
    switch (provider) {
      case 'gemini':
        return [
          'gemini-2.5-pro-preview-06-05',
          'gemini-1.5-pro',
          'gemini-1.5-flash'
        ];
      case 'grok':
        return [
          'grok-4-latest',
          'grok-4',
          'grok-beta'
        ];
      default:
        return [];
    }
  }
}

// Singleton instance
export const llmService = new LLMService();